.portfolio {
    float:left;
    width:45%;
    min-width:415px;
    margin:2%;
    border-radius:12px;
    -webkit-box-shadow: 6px 8px rgba(102, 119, 136, 0.03), 0 1px 2px rgba(102, 119, 136, 0.3), 0 8px 12px rgba(58, 193, 203, 0.1);
            box-shadow: 6px 8px rgba(102, 119, 136, 0.03), 0 1px 2px rgba(102, 119, 136, 0.3), 0 8px 12px rgba(58, 193, 203, 0.1);
}
.close-btn {
    margin-top:0.1em;
    margin-right:0.1em;
}
.portfolio-name {
    font-weight: bold;
    color:#eee;
    -ms-flex-positive: 1;
        flex-grow: 1;
}
.p-header {
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
}
.toolbar {
    margin:0.2em 1em 1em 1em;
}
.toolbar button {
    padding:0.4em 0.8em;
    font-size:80%;
}
toolbar button:nth-child(1){
    margin-right:auto;
    margin-left:0.3em;
}
toolbar button:nth-child(3){
    margin-right:0.5em;
    margin-left:auto;
}
.load-icon {
    -webkit-animation-name: load-animation;
            animation-name: load-animation;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}
.title-pane {

    background:-webkit-gradient(linear, left top, right top, color-stop(80%, #000000), to(rgba(241, 243, 245, 0)));

    background:-webkit-linear-gradient(left, #000000 80%, rgba(241, 243, 245, 0));

    background:-o-linear-gradient(left, #000000 80%, rgba(241, 243, 245, 0));

    background:linear-gradient(to right, #000000 80%, rgba(241, 243, 245, 0));
    width:100%;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
    padding:0.2em;
}

@-webkit-keyframes load-animation {
    100% {-moz-transform: rotate(360deg);}
    100% {-webkit-transform: rotate(360deg);}
    100% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
    
}

@keyframes load-animation {
    100% {-moz-transform: rotate(360deg);}
    100% {-webkit-transform: rotate(360deg);}
    100% {-webkit-transform: rotate(360deg);transform: rotate(360deg);}
    
}

@media (max-width:420px) {
    .portfolio-name {
        font-size:90%;
    }
    .portfolio {
        width: 95%;
        min-width: 304px;
        height: 30%;
        margin-top:1vw;
        -webkit-box-shadow: 6px 8px rgba(102, 119, 136, 0.03), 0 1px 2px rgba(102, 119, 136, 0.3), 0 8px 12px rgba(58, 193, 203, 0.1);
                box-shadow: 6px 8px rgba(102, 119, 136, 0.03), 0 1px 2px rgba(102, 119, 136, 0.3), 0 8px 12px rgba(58, 193, 203, 0.1);
    }
}



